  methods: {
    goBack() {
      // 返回首页，并保持原来的状态
      this.$router.push({
        path: '/home',
        query: { 
          showReportPage: this.fromReportPage ? 'true' : 'false'
        }
      });
    },
    goToHomePage() {
      // 返回首页，并保持原来的状态
